body {
  max-width: 1440px;
  background-color: #f4f4fd;
  margin: 0 auto;
}

img {
  width: 100%;
  display: block;
}

.form-section {
  height: 50px;
  background-color: rgba(54, 86, 231, .827);
  flex-wrap: wrap;
  align-content: center;
  justify-content: center;
  margin-bottom: 20px;
  display: flex;
  box-shadow: 0 4px 6px rgba(0, 0, 0, .4);
}

.form-input {
  height: 20px;
  width: 300px;
}

.gallery {
  flex-wrap: wrap;
  gap: 15px;
  padding: 0 10px;
  display: flex;
}

.photo-card {
  width: calc(25% - 11.25px);
}

.info {
  background-color: #fff;
  border-radius: 0 0 4px 4px;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
  display: flex;
  box-shadow: 0 2px 1px rgba(46, 47, 66, .08), 0 1px 1px rgba(46, 47, 66, .16), 0 1px 6px rgba(46, 47, 66, .08);
}

.info-item {
  flex-direction: column;
  align-items: center;
  margin: 0;
  display: flex;
}

.main-text {
  margin: 0 auto;
  display: block;
}

.lower-text {
  font-size: 14px;
  font-weight: 400;
}

.load-more {
  color: #fff;
  background-color: rgba(54, 86, 231, .827);
  border: 1.5px solid #000;
  border-radius: 3px;
  margin: 20px auto;
  padding: 3px 10px;
  display: block;
}

